            <div> Here's what I'm thinking: </div>
            
            <div id="names">
            </div>
        </div>

        <div id="blog">
            Type any sequence of characters into the above model and see its generated Pokemon names! 

            <br/><br/>
            I recently started learning about sequence modeling. I gave myself the project of creating
            a Pokemon name generator using the knowledge I learned about the LSTM architecture. I was able
            to create and train this model in PyTorch. Honestly, the model works much better than I expected.
            I have saved all of the learned parameters in a readable form and reimplemented the forward propagation in JavaScript. 

            <br/><br/>
            PyTorch is so nice! It's so easy to define the forward propagation and have backpropagation implemented
            for you by default. Before learning about sequence modeling, I was learning about neural networks and I
            implemented one from scratch that learned on the MNIST dataset. The most difficult part of that
            was defining the backpropagation because it was my first time ever computing Jacobians and I kept
            mixing up indices when deriving the derivates for each parameter.
        </div>
