ating the ideas in the paper "),_c('a',{staticClass:"link",attrs:{"href":"https://arxiv.org/abs/1506.02640"}},[_vm._v(" \"You Only Look Once: Unified, Real-Time Object Detection\" by Joseph Redmon, Santosh Divvala, Ross Girshick, and Ali Farhadi. ")]),_vm._v(" It seemed promising, but this is what my model output looked like. "),_c('br'),_c('br'),_c('img',{staticClass:"pixel-img",staticStyle:{"display":"block","margin":"auto","height":"250px"},attrs:{"src":"static/captcha/yolo_out.png"}}),_c('br'),_vm._v(" It seemed like the model wasn't learning where the numbers actually were, but how to efficiently place bounding boxes in a way that were sometimes right. I tried changing the model's architecture, increasing its capacity, and changing hyper-parameters, but nothing seemed to change the model's behavior. I tried putting less numbers in my input images, but then the model tended to predict that no numbers were contained in the image. This is where I am now. I'm not sure why my model isn't working. Maybe I should train it on the same dataset used in the YOLO research paper to confirm I implemented it correctly. "),_c('br'),_c('br'),_c('div',{staticClass:"quote"},[_vm._v(" Key takeaways ")]),_c('ul',[_c('li',[_vm._v(" Data preparation matters "),_c('span',{staticClass:"italic"},[_vm._v("a lot")]),_vm._v(". Clean data can be much better than more data. ")]),_c('li',[_vm._v(" Designing deep network architectures is difficult. ")]),_c('li',[_vm._v(" Micro-changes to the model's architecture should be done on an exponential scale for asymptotic analysis. ")]),_c('li',[_vm._v(" It can be hard to figure out why a model isn't performing well. Is it a problem with data? architecture? optimization? hyper-parameters? ")])]),_vm._v(" Honestly, I think neural networks are great only for approximating certain types of functions. I think the function approximation problem needs to be re-evaluated at its most simple scale. I had an idea to use Fourier Transforms for function approxim